/* Flickering animation */
@-webkit-keyframes flicker {
    
  0%, 18%, 22%, 25%, 53%, 57%, 100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px rgb(17, 0, 255),
      0 0 80px rgb(174, 0, 255),
      0 0 90px rgb(0, 4, 255),
      0 0 100px rgb(0, 132, 255),
      0 0 150px rgb(255, 0, 212);
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}
@keyframes flicker {
    
  0%, 18%, 22%, 25%, 53%, 57%, 100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px rgb(17, 0, 255),
      0 0 80px rgb(174, 0, 255),
      0 0 90px rgb(0, 4, 255),
      0 0 100px rgb(0, 132, 255),
      0 0 150px rgb(255, 0, 212);
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}
